<template>
<div class="center">  
    <div>
        <h3>Work experience</h3>
        <hr>
        <WorkBox :works="work"/>
        <h3 style="margin-top: 20px;">Studies</h3>
        <hr>
        <StudyBox :studies="studies"/>
    </div>   
 </div>    
</template>

<script>
import WorkBox from '@/components/WorkBox.vue';
import StudyBox from '@/components/StudyBox.vue'

export default ({
    components: {
        WorkBox,
        StudyBox
    },
    created() {
        this.$store.dispatch('randomizeWork');
        this.$store.dispatch('randomizeStudies');
    },
    computed: {
        work() {
            return this.$store.getters.work;
        },
        studies(){
            return this.$store.getters.studies;
        }
    }  
});
</script>